import React from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import { Card } from '../Card/Card';

import './List.css';

export const List = ({ allItems, pageSize = 20 }) => {
    const [items, setitems] = React.useState([]);
    const [hasMore, sethasMore] = React.useState(true);

    React.useEffect(() => {
        setitems(allItems.slice(0, pageSize));
        sethasMore(allItems.length > pageSize);
    }, [allItems, pageSize])

    const fetchMoreData = () => {
        if (items.length >= allItems.length) {
            sethasMore(false);
            return
        }

        let append = [];
        for (let i = items.length; i < allItems.length && i < items.length + pageSize; i++) {
            append.push(allItems[i]);
        }

        setitems([...items, ...append])
    }

    return (
        <>
            <InfiniteScroll
                dataLength={items.length}
                next={fetchMoreData}
                hasMore={hasMore}
                loader={<h4>Loading...</h4>}
                endMessage={
                    <p style={{ textAlign: "center" }}>
                        <b></b>
                    </p>
                }
            >
                <div className="card-box">
                    {items.map((item, index) => <Card key={index} item={item} />)}
                </div>
            </InfiniteScroll>
        </>
    )
}