import React from "react";
import Providers from "./Providers";
import { HomeView } from "./views/Home/Home";
import { Profile } from './views/profile';
import { ShowRoom } from './views/showroom';
import { WalletGuide } from './views/guide/WalletGuide';
import { BscGuide } from './views/guide/BscGuide';

import { HashRouter, Route, Routes } from "react-router-dom";

function CustomRoutes() {
    return (
        <HashRouter basename={'/'}>
            <Providers>
                <Routes>
                    <Route exact path="/profile" element={<Profile />} />
                    <Route exact path="/guide/wallet" element={<WalletGuide />} />
                    <Route exact path="/guide/bsc" element={<BscGuide />} />
                    <Route path="/collection/:collectionName" element={<ShowRoom />} />
                    <Route path="/" element={<HomeView />} />
                </Routes>
            </Providers>
        </HashRouter>
    )
}

export default CustomRoutes;