import React from 'react';
import { List } from '../components/List/List';
import { useMetadata } from '../providers/MetadataProvider';
import { AttributeGroupFilter } from '../components/Filter/Filter';
import TextField from '@mui/material/TextField';
import { useParams } from "react-router-dom";


import './showroom.css';

function attributeMatched(item, filters) {

    for (const group of Object.keys(filters)) {
        for (const value of filters[group]) {
            if (!item.attrsMap[value]) { return false }
        }
    }
    return true
}

export const ShowRoom = () => {
    const params = useParams();
    const collection = useMetadata(params.collectionName);
    const [filters, setfilters] = React.useState({});
    const [items, setitems] = React.useState([]);
    const [tokenId, settokenId] = React.useState(0);

    const allItems = React.useMemo(() => {
        if (!collection) { return [] }
        return collection.items.map(item => {
            let m = {};
            item.attributes.forEach(attr => m[attr.value] = 1);
            item.attrsMap = m;
            return item
        })
    }, [collection]);

    React.useEffect(() => {
        if (!collection) { return [] }
        setitems(allItems);
    }, [collection, allItems])

    const attrGroups = React.useMemo(() => {
        if (!collection) {
            return
        }

        return Object.keys(collection.attributes).filter(key => key !== '历史长度').map(key => {
            const group = collection.attributes[key];
            return {
                name: key,
                attributes: Object.keys(group).map(key => {
                    return {
                        value: key,
                        count: group[key],
                        rarity: group[key] / collection.items.length
                    }
                }),
            }
        })
    }, [collection])

    const onFilterChange = React.useCallback(({ group, attrs }) => {
        filters[group] = attrs;
        setfilters(filters)
        settokenId(0);
        setitems(allItems.filter(item => attributeMatched(item, filters)));
    }, [filters, allItems])
    
    const onTokenIdChagne = React.useCallback(({ target: { value } }) => {
        value = Number(value);
        settokenId(value);
        setfilters({})
        if (value === 0) {
            setitems(allItems)
        } else {
            setitems(allItems.filter(item => item.token_id === value));
        }
        
    }, [settokenId, setfilters, setitems, allItems])


    return (
        <>{
            collection ?
                <>
                    <div className="collection-meta">
                        <h1 className="title">{collection.collection.name}</h1>
                        {/* <p className="description">{collection.description}</p> */}
                        <p>
                            <a className="contract" target="__blank" href="https://bscscan.com/address/0xf955162dc438913b4660e892a75d0f9767f6f48e">Contract: 0xf955162dc438913b4660e892a75d0f9767f6f48e</a>
                        </p>
                        
                    </div>
                    <div className="items-box">
                        <div className="items-filter-box">
                            <p className="items-count">{`共${items.length}项`}</p>
                            <TextField sx={{ m: 1, width: 250 }}
                                type="number"
                                color="secondary"
                                label="编号"
                                value={tokenId}
                                onChange={onTokenIdChagne}
                                variant="outlined" />

                            {
                                attrGroups.map(group => <AttributeGroupFilter
                                    key={group.name}
                                    groupName={group.name}
                                    attributes={group.attributes.sort((a, b) => a.rarity - b.rarity)}
                                    onChange={onFilterChange} />)
                            }
                        </div>
                        <div className="items-list">
                            <List allItems={items} />
                        </div>

                    </div>
                </>
                : <p>加载中</p>

        }

        </>
    )
}