import React from 'react';

import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import { AddressText } from '../AddressText/AddressText';
import { WalletSelect } from '../../providers/WalletSelect';
import { useWeb3 } from '../../providers/Web3';
import { useNavigate } from 'react-router-dom';
import './WalletMenu.css';

export const WalletMenu = () => {
  const { connect, disconnect, account } = useWeb3();
  const navigate = useNavigate();

  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };




  const menu = () => {
    return (
      <>
        <Menu
          id="basic-menu"
          anchorEl={anchorEl}
          open={open}
          onClose={handleClose}
          MenuListProps={{
            'aria-labelledby': 'basic-button',
          }}
        >
          <MenuItem onClick={() => navigate('/profile')}>我的收藏</MenuItem>
          <MenuItem onClick={disconnect}>断开连接</MenuItem>
        </Menu>
      </>
    )
  }


  return (
    <div className="wallet-menu">
      {
        account ? <>
          <button className="wallet-connect-btn" onClick={handleClick}>
            <AddressText address={account} />
          </button>
          {menu()}
        </>
          :
      <button className="wallet-connect-btn" onClick={connect}>连接钱包</button>
      } 
    </div>
  )
}

