import React from 'react';
import { useMetadata } from '../providers/MetadataProvider';
import { useNationContract } from '../providers/NationContract';
import { useWeb3 } from '../providers/Web3';
import { useNavigate } from 'react-router';
import { List } from '../components/List/List';

export const Profile = () => {
    const { account } = useWeb3();
    const navigate = useNavigate();
    const nationContract = useNationContract();
    const md = useMetadata();
    const [ownedItems, setownedItems] = React.useState([]);



    React.useEffect(() => {

        async function fetchOwned() {
            let count = await nationContract.methods.balanceOf(account).call();
            let idx = [];
            for (let i = 0; i < count; i++) {
                idx.push(i);
            }

            let tokenIds = await Promise.all(
                idx.map(i => {
                    return nationContract.methods.tokenOfOwnerByIndex(account, i).call()
                })
            );


            let items = tokenIds.map(tokenId => {
                return md.items[tokenId - 1]
            });

            setownedItems(items)
        }

        if (!account) {
            navigate("/");
        }

        if (!account || !nationContract || !md) {
            return
        }


        fetchOwned()

    }, [account, nationContract, navigate, md])

    return (
        <>
            {account && <List allItems={ownedItems} />}
        </>
    )
}
