import * as React from 'react';
import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Chip from '@mui/material/Chip';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
      color: '#000'
    },
  },
};

function getStyles(name, attrName, theme) {
  return {
    fontWeight:
      attrName.indexOf(name) === -1
        ? theme.typography.fontWeightRegular
        : theme.typography.fontWeightMedium,
  };
}

export const AttributeGroupFilter = ({ groupName, attributes, onChange }) => {
  const theme = useTheme();
  const [attrName, setattrName] = React.useState([]);


  const handleChange = (event) => {
    const {
      target: { value },
    } = event;
    const attrs = typeof value === 'string' ? value.split(',') : value;
    onChange && onChange({ group: groupName, attrs })
    setattrName(attrs);
  };

  return (
    <div>
      <FormControl sx={{ m: 1, width: 250 }}>
        <InputLabel color="secondary" id="demo-multiple-chip-label">{groupName}</InputLabel>
        <Select
          labelId="demo-multiple-chip-label"
          id="demo-multiple-chip"
          multiple
          value={attrName}
          onChange={handleChange}
          input={<OutlinedInput color="secondary" id="select-multiple-chip" label={groupName} />}
          renderValue={(selected) => (
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
              {selected.map((value) => (
                <Chip key={value} label={value} />
              ))}
            </Box>
          )}
          MenuProps={MenuProps}
        >
          {attributes.map((attr) => (
            <MenuItem
              key={attr.value}
              value={attr.value}
              style={getStyles(attr.value, attrName, theme)}
            >
              {`${attr.value} - 稀有度${(attr.rarity * 100).toFixed(2)}%`}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </div>
  );
}